<template>
  <div class="create">
    <!--组件库 应该是个异步的 配置url可以自动获取-->
    <div style="width: 200px;overflow-y: auto;overflow-x: hidden" class="btn-box">
      <button ref="initEvt" class="btn" @click="initEvt">初始化事件</button>
      <button ref="initTreeEvt" class="btn" @click="initTreeEvt">初始化画布</button>
      <button ref="add" class="btn" @click="add">添加到画布</button>
      <button ref="add2" class="btn" @click="add2">添加到指定父级</button>
      <div>ZIndex操作</div>
      <button ref="bringForward" class="btn" @click="zMove('bringForward')">上</button>
      <button ref="sendBackward" class="btn" @click="zMove('sendBackward')">下</button>
      <button ref="bringToFront" class="btn" @click="zMove('bringToFront')">顶</button>
      <button ref="sendToBack" class="btn" @click="zMove('sendToBack')">底</button>
      <div>位置移动</div>
      <input type="text" v-model="movestep"/>
      <button ref="up" class="btn" @click="move('up')">上</button>
      <button ref="down" class="btn" @click="move('down')">下</button>
      <button ref="left" class="btn" @click="move('left')">左</button>
      <button ref="right" class="btn" @click="move('right')">右</button>
      <div>数据操作</div>
      <button ref="copy" class="btn" @click="copy">复制选中项</button>
      <button ref="paste" class="btn" @click="paste">粘贴</button>
      <button ref="undo" class="btn" @click="undo">撤销</button>
      <button ref="group" class="btn" @click="group">编组</button>
      <button ref="breakGroup" class="btn" @click="breakGroup">打散</button>
      <button ref="deleteSelect" class="btn" @click="deleteSelect">删除选中</button>
      <button ref="scale" class="btn" @click="scale">等比缩放</button>
      <div>画布数据修改</div>
      <div class="edit">
        <input type="text" v-model="width"/>
        <button ref="edit" class="btn" @click="edit">修改画布尺寸</button>
        <button ref="edit" class="btn" @click="edit2">edit2</button>
      </div>

    </div>

    <!--画布-区分 自动布局还是绝对布局-->
    <pdDragBase ref="sketch"
                :ruleConfig="{
                  frameSelectType:'touch',
                  stackLength:20,
                   //scale:3.7795276/*mm 2 px*/
                   scale:1/*mm 2 px*/
            }"
    >
      <droot :renderData="root"></droot>
    </pdDragBase>

    <!--属性栏-->
    <div style="width: 200px">
      <input type="text"/>
      {{ selected }}

    </div>
  </div>
</template>

<script>


export default {
  name: "create",
  data() {
    return {
      rightMenu: [{
        type: "xxx",
        evt: this.rightText
      }],
      sketch: {selected: []},
      selected: [],
      width: 100,
      movestep: 10,
      lock: false,
      initTree: {
        type: 'sketchup',
        width: 100,
        height: 200,
        to: 0,
        left: 0,
        children: []
      },
      root: {}
    }

  },
  mounted() {
  },
  methods: {
    initEvt() {
      this.sketch = this.$refs.sketch;
      this.sketch.$bind('addend', (res) => {
        this.sketch.select({id: res[ 0 ]})
      })
      document.addEventListener('keyup', (e) => {
        if (this.shift) {
          this.sketch.$pureFire('shift', false)
        }
      })
      document.addEventListener('keydown', (e) => {
        if (e.shiftKey) {
          this.shift = true
          this.sketch.$pureFire('shift', true)
        }
      })
      this.inited = true;
    },
    scale() {
      this.lock = !this.lock
      this.sketch.lock({isLock: this.lock})
    },
    initTreeEvt() {
      this.sketch.initTree({
        data: this.initTree,
        snapShot: false
      })
    },
    zMove(type) {
      this.sketch.zIndex({type, id: this.selected[ 0 ].id})
    },
    move(direction) {
      this.sketch.move({direction})
    },
    /*----*/
    copy() {
      this.sketch.copy()
    },
    paste() {
      this.sketch.paste()
    },
    undo() {
      this.sketch.undo()
    },
    group() {
      this.sketch.group()
    },
    breakGroup() {
      this.sketch.breakGroup()
    },
    deleteSelect() {
      this.sketch.deleteSelect()
    },
    /*----*/
    add() {
      this.sketch.addToSketch(
          {
            item: [{
              "left": 148,
              "top": 63,

              "rotate": 0,
              "width": 31,
              "height": 38,
              "active": true,
              "childActiveAble": false,
              "type": "dtext",
              "children": [],
              "id": "e7c02a03-0142-4af1-a49d-2e09461d1909",
              "parentId": "4d7438ad-fe8c-4356-89f0-b0385c81b811"
            }, {
              "left": 58,
              "top": 87,
              "width": 50,
              "height": 50,
              "active": false,
              "childActiveAble": false,
              "type": "dtext",
              "children": [],
              "id": "16ddeef0-9c50-477f-8ff7-07d24e7d8920",
              "parentId": "4d7438ad-fe8c-4356-89f0-b0385c81b811",
              "initXY": {}
            }, {
              "left": 6,
              "top": 3,
              "width": 56,
              "height": 75,
              "type": "dragdropbox",
              "id": "432cb02c-a9fa-4a48-baa5-0c529636243a",
              "parentId": "4d7438ad-fe8c-4356-89f0-b0385c81b811",
              "initXY": {},
              "active": false,
              "childActiveAble": false,
              "children": [{
                "left": 0,
                "top": 0,

                "width": 42,
                "height": 43,
                "active": false,
                "childActiveAble": false,
                "type": "dtext",
                "children": [],
                "id": "6547406f-103a-4a12-b65f-599a876bb712",
                "parentId": "432cb02c-a9fa-4a48-baa5-0c529636243a",
                "initXY": {}
              }, {
                "left": 28,
                "top": 57,
                "width": 28,
                "height": 18,
                "active": false,
                "childActiveAble": false,
                "type": "dtext",
                "children": [],
                "id": "25f597b4-955b-439a-9157-38220f46040f",
                "parentId": "432cb02c-a9fa-4a48-baa5-0c529636243a",
                "initXY": {}
              }]
            }]
          }
      )
    },
    add2() {
      this.sketch.addToSketch({
        parent: this.selected.length === 1 ? this.selected[ 0 ] : null,
        item: [{
          "left": 10,
          "top": 20,
          "width": 50,
          "height": 50,
          "active": false,
          rotateable: true,
          "childActiveAble": false,
          "type": "dtext",
          "children": [],
          "id": "4b133596-62ee-4e70-b3ae-bd93a5482fa6",
          "parentId": null,
          "initXY": {}
        }]
      })
      // const component = {
      //   "type": '组件名',
      //   "left": 0,
      //   "top": 0,
      //   
      //   "rotate": 0,
      //   "width": 0,
      //   "height": 0,
      //   "active": false,
      //   "childActiveAble": false,
      //   "id": "0",
      //   "parentId": null,
      //   "children": []
      // }

    },
    edit() {

      this.sketch.updateItem({
        id: this.root.id, data: {
          customAttribute: {
            dataSource: 'xxxxx',
            abc: {
              ef: 123
            },
            eef: 213
          }
        }
      })
      // this.sketch.updateItem({
      //   id: this.selected[0].id, data: {
      //     width: 100,
      //     customAttribute: {
      //       dataSource: 'xxxxx',
      //       abc: {
      //         ef: 123
      //       }
      //     }
      //   }
      // })
    },
    edit2() {
      this.sketch.updateItem({id: this.$refs.sketch.sketchupData.id, data: {customAttribute: {eef: 1}}})
      // this.sketch.updateItem({
      //   id: this.selected[0].id, data: {
      //     width: 100, customAttribute: {
      //       abc: {
      //         ff: 123
      //       }
      //     }
      //   }
      // })
    },
    beforeCopy(val) {
      console.log(val, 'beforeCopy')
      /*请添加加载动画防止用户误操作等待异步完成*/
      return new Promise(( (resolve) => {
        //对 复制数据异步加工 val
        resolve(val)
      } ))

      /*也支持同步*/
      /*return val */
    },
    beforeDelete(val) {
      console.log(val, 'beforeDelete')
      return true
    }
  },
  watch: {
    'sketch.selected': {
      handler(val) {
        this.selected = val
      },
      deep: true
    },
    'sketch.sketchupData'(val) {
      this.root = val
    }
  }
}
</script>

<style lang="scss">
body {
  overflow: hidden;
}


.create {
  display: flex;
  height: 100%;
  flex: 1;
}

.btn-box {
  display: flex;
  flex-direction: column;

  .btn {
    display: flex;
    width: 100%;
    margin-bottom: 5px;
  }
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

html, body {
  width: 100%;
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
}
</style>