/**
 * 从node开始网上查找满足 hasAttribute 有name的node ，并且不超过topNode
 * @param topNode
 * @param node
 * @param name
 * @returns {Element|null}
 */
export function getTriggerNode(topNode = null, node, name = null) {
  if (topNode && topNode.nodeType !== 1) {
    return null
  }
  if (node && node.nodeType !== 1) {
    return null
  }
  if (!node || !name) {
    return null
  }
  if (node.hasAttribute && node.hasAttribute(name)) {
    return node
  }
  if (node.parentNode && node.parentNode !== topNode) {
    return getTriggerNode(topNode, node.parentNode, name)
  } else {
    return null
  }
}

/**
 * 生成唯一id
 * @returns {string}
 */
export function uuid() {
  var temp_url = URL.createObjectURL(new Blob());
  var uuid = temp_url.toString(); // blob:https://xxx.com/b250d159-e1b6-4a87-9002-885d90033be3
  URL.revokeObjectURL(temp_url);
  return uuid.substr(uuid.lastIndexOf("/") + 1);
}

/**
 * 是否是Promise
 * @param obj
 * @returns {boolean}
 */
export function isPromise(obj) {

  return !!obj && (typeof obj === 'object' || typeof obj === 'function') && typeof obj.then === 'function';

}

/**
 * 是否是数组
 * @param o
 * @returns {boolean}
 */
export function isArray(o) {

  return Object.prototype.toString.call(o) == '[object Array]'
}

/**
 * 节流函数
 * @param fn
 * @param delay
 * @returns {Function}
 */
export function throttle(fn, delay = 1000) {
  let timer = null;
  let firstTime = true;

  return function (...args) {
    if (firstTime) {
      // 第一次加载
      fn.apply(this, args);
      return firstTime = false;
    }
    if (timer) {
      // 定时器正在执行中，跳过
      return;
    }
    timer = setTimeout(() => {
      clearTimeout(timer);
      timer = null;
      fn.apply(this, args);
    }, delay);
  };
}

/**
 * 是否是空对象
 * @param o
 * @returns {boolean}
 */
export function isEmptyOb(o) {
  if (o) {
    return Object.keys(o).length === 0
  } else {
    return false
  }
}

/**
 * 获取 一组offset 内的方位坐标
 * @param arr
 * @returns {{top: number, left: number, width: number, height: number}}
 */
export function getAxis(arr = []) {
  let xMin = 0, yMin = 0, xMax = 0, yMax = 0, pass = true;
  if (isArray(arr)) {
    for (let i = 0; i < arr.length; ++i) {
      let item = arr[i];
      if (!item) {
        continue
      }
      if (item.left === undefined || item.top === undefined || item.width === undefined || item.height === undefined) {
        pass = false
        break
      }
      // 起始位置信息
      const {mx, my} = {
        mx: item.left,
        my: item.top
      };
      // 位移向量
      // const center = {x: mx + item.width / 2, y: my + item.height / 2};
      //
      // //todo 旋转后的坐标系转换，获取新的坐标点公式如下：
      // // x'=r·cos(θ)
      // // y'=r·sin(θ)
      // let rotate = item.rotate || 0;
      // rotate = rotate * (Math.PI / 180)
      // //4个点坐标
      // /*
      // * x1,y1------x2,y2
      // * |           |
      // * |           |
      // * |-----------|
      // * x4,y4       x3,y3
      // *
      // * */
      // let x1 = (item.left - center.x) * Math.cos(rotate) - (item.top - center.y) * Math.sin(rotate) + center.x
      // let y1 = (item.left - center.x) * Math.sin(rotate) - (item.top - center.y) * Math.cos(rotate) + center.y

      // let x2 = (item.left + item.width - center.x) * Math.cos(rotate) - (item.top - center.y) * Math.sin(rotate) + center.x
      // let y2 = (item.left + item.width - center.x) * Math.sin(rotate) - (item.top - center.y) * Math.cos(rotate) + center.y
      //
      // let x3 = (item.left + item.width - center.x) * Math.cos(rotate) - (item.top + item.height - center.y) * Math.sin(rotate) + center.x
      // let y3 = (item.left + item.width - center.x) * Math.sin(rotate) - (item.top + item.height - center.y) * Math.cos(rotate) + center.y
      //
      // let x4 = (item.left - center.x) * Math.cos(rotate) - (item.top + item.height - center.y) * Math.sin(rotate) + center.x
      // let y4 = (item.left - center.x) * Math.sin(rotate) - (item.top + item.height - center.y) * Math.cos(rotate) + center.y


      if (i === 0) {
        xMin = item.left
        yMin = item.top
      }
      if (item.left <= xMin) {
        xMin = item.left
      }
      if (item.top <= yMin) {
        yMin = item.top
      }
      let x = Number(item.left) + Number(item.width)
      if (x >= xMax) {
        xMax = x
      }
      let y = Number(item.top) + Number(item.height)
      if (y >= yMax) {
        yMax = y
      }
    }
  }
  return pass ? {
    left: xMin,
    top: yMin,
    width: xMax - xMin,
    height: yMax - yMin
  } : {
    left: 0,
    top: 0,
    width: 0,
    height: 0
  }
}

/**
 *
 * @param fn
 * @returns {boolean}
 */
export function isFunction(fn) {
  return Object.prototype.toString.call(fn) === '[object Function]';
}

/**
 * 碰撞检测
 * @param draggable
 * @param droppable
 * @param toleranceMode
 * @param event
 * @returns {boolean}
 */
export function intersect(draggable, droppable, toleranceMode, event) {
  let x1 = draggable.left,
    y1 = draggable.top,
    x2 = draggable.right,
    y2 = draggable.bottom,
    l = droppable.left,
    t = droppable.top,
    r = droppable.right,
    b = droppable.bottom;
  switch (toleranceMode) {
    case "fit"://完全进入
      return (l <= x1 && x2 <= r && t <= y1 && y2 <= b);
    case "intersect_x":// x 比较交叉一半
      return (l < x1 + ((draggable.right - draggable.left) / 2) && // Right Half
        x2 - ((draggable.right - draggable.left) / 2) < r  // Left Half
      );
    case "intersect"://全部交叉1半
      return (l < x1 + ((draggable.right - draggable.left) / 2) && // Right Half
        x2 - ((draggable.right - draggable.left) / 2) < r && // Left Half
        t < y1 + ((draggable.bottom - draggable.top) / 2) && // Bottom Half
        y2 - ((draggable.bottom - draggable.top) / 2) < b); // Top Half
    case "pointer"://鼠标进入
      return isOverAxis(event.pageY, t, (draggable.bottom - draggable.top)) &&
        isOverAxis(event.pageX, l, (draggable.right - draggable.left));
    case "touch"://挨着
      return (
        (y1 >= t && y1 <= b) || // Top edge touching
        (y2 >= t && y2 <= b) || // Bottom edge touching
        (y1 < t && y2 > b) // Surrounded vertically
      ) && (
        (x1 >= l && x1 <= r) || // Left edge touching
        (x2 >= l && x2 <= r) || // Right edge touching
        (x1 < l && x2 > r) // Surrounded horizontally
      );
    default:
      return false;
  }
}


/**
 * x 是否在 reference 到 reference+size之间
 * @param x
 * @param reference
 * @param size
 * @returns {boolean}
 */
function isOverAxis(x, reference, size) {
  return (x >= reference) && (x < (reference + size));
}

/**
 *
 * @param element
 * @param className
 * @returns {boolean}
 */
export function hasClass(element, className) {
  do {
    if (element.classList && element.classList.contains(className)) {
      return true;
    }
    element = element.parentNode;
  } while (element);
  return false;
}

export function cleanSelect(){
  if (window.getSelection) {
    if (window.getSelection().empty) {  // Chrome
      window.getSelection().empty();
    } else if (window.getSelection().removeAllRanges) {  // Firefox
      window.getSelection().removeAllRanges();
    }
  } else if (document.selection) {  // IE?
    document.selection.empty();
  }
}