<template>
  <div
      :class="{ 'vdr-active': active && activeable, 'vdr-not-active': !activeable }"
      :style="style"
      @mousedown="bodyDown($event)"
      class="vdr"
      ref="vdr"
  >
    <!-- 控件 -->
    <template v-if="active && activeable">
      <!-- 缩放控件 -->
      <template v-if="resizeable">
        <span
            :class="`vdr-stick-${stick}`"
            :key="stickIndex"
            :style="{ zIndex: activeStick == stickIndex ? 10 : 9 }"
            @mousedown.stop.prevent="stickDown($event, stick, stickIndex)"
            class="vdr-stick"
            v-for="(stick, stickIndex) in rotateSticks"
        ></span>
      </template>
      <!-- 旋转控件 -->
      <template v-if="sticks.indexOf('angle') > -1&&rotateable">
        <span class="vdr-stick-rotate-line"></span>
        <span
            @mousedown.stop.prevent="rotateDown($event)"
            class="vdr-stick vdr-rotate"
        ></span>
      </template>
    </template>

    <!-- 插槽 -->
    <!-- 插槽 -->
    <div
        class="vdr-slot"
        ref="slot"
    >
      <slot></slot>
    </div>
  </div>
</template>
<script>
// 旋转坐标点缓存
let pointA = {};
let pointB = {};
let pointC = {};

export default {
  name: "pdDragResizeRotate",
  props: {
    snap: {
      type: Boolean,
      default: true
    },
    snapTolerance: {
      type: Number,
      default: 5,
      validator: function (val) {
        return typeof val === 'number'
      }
    },
    hidden: {
      type: Boolean,
      default: false
    },
    lock: {
      type: Boolean,
      default: false
    },
    w: {
      type: Number,
      default: 100,
      validator: function (val) {
        return val >= 0;
      }
    },
    h: {
      type: Number,
      default: 100,
      validator: function (val) {
        return val >= 0;
      }
    },
    x: {
      type: Number,
      default: 0,
      validator: function (val) {
        return typeof val === "number";
      }
    },
    y: {
      type: Number,
      default: 0,
      validator: function (val) {
        return typeof val === "number";
      }
    },
    z: {
      type: [String, Number],
      default: "auto"
    },
    r: {
      type: Number,
      default: 0
    },
    sticks: {
      type: Array,
      default: function () {
        return ["tl", "tm", "tr", "mr", "br", "bm", "bl", "ml", "angle"];
      }
    },
    active: {
      type: Boolean,
      default: false
    },
    draggable: {
      type: Boolean,
      default: true
    },
    resizeable: {
      type: Boolean,
      default: true
    },
    rotateable: {
      type: Boolean,
      default: true
    },
    activeable: {
      type: Boolean,
      default: true
    },

    widthRange: {
      type: Array
    },
    heightRange: {
      type: Array
    },
    rulerScale: {
      type: [Number, String],
      default: 1
    }
  },
  data() {
    return {
      whRatio: 0,
      width: this.w,
      height: this.h,
      zIndex: this.z,
      left: this.x,
      top: this.y,
      bottom: 0,
      right: 0,
      rotate: this.r,
      activeStick: -1,
      currentStick: "",
      moving: false
    };
  },
  computed: {
    style() {
      const rotate = `rotateZ(${Math.round(this.rotate)}deg)`;
      const translate = `translate3d(${this.left}px,${this.top}px,0)`;
      return {
        zIndex: this.moving ? 99999 : this.zIndex,
        width: `${this.width}px`,
        height: `${this.height}px`,
        transform: `${translate} ${rotate}`,
        visibility: this.hidden ? "hidden" : "visible"
      };
    },
    rotateSticks() {
      return this.sticks.filter(itme => itme !== "angle");
    },
    posData() {
      return {
        left: this.left,
        top: this.top,
        width: this.width,
        height: this.height,
        rotate: Math.round(this.rotate),
        stick: this.currentStick
      };
    },
    // x的最小偏移值
    minOffsetLeft() {
      return this.parentWidth - this.right - this.minWidth;
    },
    maxOffsetLeft() {
      return this.parentWidth - this.right - this.maxWidth;
    },
    // y的最小偏移值
    minOffsetTop() {
      return this.parentHeight - this.bottom - this.minHeight;
    },
    maxOffsetTop() {
      return this.parentHeight - this.bottom - this.maxHeight;
    }
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    // 销毁前移除事件
    document.documentElement.removeEventListener("mousemove", this.move);
    document.documentElement.removeEventListener("mouseup", this.up);
  },
  methods: {
    getScale() {
      return parseFloat(1 / this.rulerScale)
    },
    // 计算最小宽
    calcMinWidth() {
      const min = Math.min.apply(null, this.widthRange);
      if (min < 0 || (min >= 0 && min >= this.width)) return 0;
      return min || 0;
    },
    // 计算最大宽
    calcMaxWidth() {
      const max = Math.max.apply(null, this.widthRange);
      if (max <= 0 || (max > 0 && max < this.width)) return Infinity;
      return max || Infinity;
    },
    // 计算最小高
    calcMinHeight() {
      let min = Math.min.apply(null, this.heightRange);
      if (min < 0 || (min >= 0 && min >= this.height)) min = 0;
      if (this.lock) {
        if (this.widthRange) {
          return this.minWidth / this.whRatio;
        } else if (this.widthRange == null && this.heightRange) {
          this.minWidth = min * this.whRatio;
        }
      }
      return min || 0;
    },
    // 计算最大高
    calcMaxHeight() {
      let max = Math.max.apply(null, this.heightRange);
      if (max <= 0 || (max > 0 && max < this.height)) max = Infinity;
      if (this.lock) {
        if (this.widthRange) {
          return this.maxWidth / this.whRatio;
        } else if (this.widthRange == null && this.heightRange) {
          this.maxWidth = max * this.whRatio;
        }
      }
      return max || Infinity;
    },
    // 范围值初始
    setLimitValue() {
      this.minWidth = this.calcMinWidth();
      this.maxWidth = this.calcMaxWidth();
      this.minHeight = this.calcMinHeight();
      this.maxHeight = this.calcMaxHeight();
    },
    // 获取所有父旋转角的叠加状态角
    getParentsRotate(ev) {
      let rotate = 0;
      let path = ev.path || (ev.composedPath && ev.composedPath()) || [];
      path = path.filter(
          element =>
              element.className && element.className.match("vdr-slot") == null
      );
      const len = path.length || 0;
      if (len < 1) return 0;
      //自身index为0， >0 过滤掉自身
      //如果有slot 则需要过滤掉自身slot
      for (let i = len - 1; i > 0; i--) {
        const element = path[i];
        // 过滤掉window和document
        if (element === window || element === document) continue;
        if (element === this.$el) continue;
        rotate += this.getElementRotate(element);
      }
      return rotate;
    },
    // 获取元素旋转角度(矩阵转换)
    getElementRotate(element) {
      if (element == null) return 0;
      const parentStyle = window.getComputedStyle(element, null);
      const matrixInfo =
          parentStyle["-webkit-transform"] ||
          parentStyle["-moz-transform"] ||
          parentStyle["-ms-transform"] ||
          parentStyle["-o-transform"] ||
          parentStyle["transform"];

      if (matrixInfo.match("matrix") == null) return 0;

      const matrix = matrixInfo.replace(/matrix\(|\)|\s/gi, "");
      const matrixArray = matrix.split(",") || [];
      const a = Number(matrixArray[0]);
      const b = Number(matrixArray[1]);
      const angle = Math.round(Math.atan2(b, a) * (180 / Math.PI));
      return angle || 0;
    },
    // 初始化
    init() {
      // 元素宽高比例初始化
      this.whRatio = this.width / this.height;

      // 父级信息初始化
      this.parentElement = this.$el.parentNode;
      this.parentWidth = this.parentElement.clientWidth;
      this.parentHeight = this.parentElement.clientHeight;

      // 元素bottom、right初始化
      this.bottom = this.parentHeight - this.height - this.top;
      this.right = this.parentWidth - this.width - this.left;

      // 范围值初始化
      this.setLimitValue();

      // 鼠标、元素位置信息初始化
      this.bodyStartPos = {
        mx: 0,
        my: 0,
        left: 0,
        top: 0,
        right: 0,
        bottom: 0
      };
      // 元素的mousemove、mouseup事件委托到document.documentElement
      document.documentElement.addEventListener("mousemove", this.move);
      document.documentElement.addEventListener("mouseup", this.up);
    },
    //元素本身的mousedown事件回调函数
    bodyDown(ev) {
      if (!this.activeable) return;
      ev.stopPropagation();
      this.bodyDrag = true;
      this.currentStick = "";

      // 记录开始鼠标位置
      this.bodyStartPos.mx = ev.clientX;
      this.bodyStartPos.my = ev.clientY;
      // 记录开始元素位置
      this.bodyStartPos.left = this.left;
      this.bodyStartPos.top = this.top;

      this.parentsRotate = this.getParentsRotate(ev);

      // 触发事件
      if (this.activeable) {
        this.$emit("activated", ev, this.posData);
        this.$emit("dragStart", this.posData);
        //插入虚拟 vdr
        this.createVdrs()
      }
    },
    // 元素本身的mousemove事件回调函数
    async bodyMove(ev) {
      this.moving = true
      // 起始位置信息
      const {mx, my, left, top} = this.bodyStartPos;
      // 位移向量
      const vector = {x: ev.clientX - mx, y: ev.clientY - my};
      // 父元素旋转后的坐标系转换，获取新的坐标点公式如下：
      // x'=x·cos(θ)+y·sin(θ)
      // y'=y·cos(θ)-x·sin(θ)

      const rad = this.parentsRotate * (Math.PI / 180);

      const x = vector.x * Math.cos(rad) + vector.y * Math.sin(rad);
      const y = vector.y * Math.cos(rad) - vector.x * Math.sin(rad);

      // 更新位置信息
      let scale = this.getScale();

      this.left = left + x * scale;
      this.top = top + y * scale;
      if (this.snap) {
        await this.snapCheck();
      }

      // 触发拖拽事件
      this.$emit("dragging", this.posData);
    },

    //缩放控件的mousedown事件回调函数
    stickDown(ev, stick, index) {
      if (!this.activeable) return;

      this.stickDrag = true;
      this.activeStick = index;
      // 记录当前拖拽的stick
      this.currentStick = stick;

      // 记录开始时鼠标位置
      this.bodyStartPos.mx = ev.clientX;
      this.bodyStartPos.my = ev.clientY;

      // 计算开始时元素right、bottom位置信息
      this.right = this.parentWidth - this.width - this.left;
      this.bottom = this.parentHeight - this.height - this.top;

      // 记录开始时元素位置
      this.bodyStartPos.left = this.left;
      this.bodyStartPos.top = this.top;
      this.bodyStartPos.bottom = this.bottom;
      this.bodyStartPos.right = this.right;

      // 触发事件
      this.$emit("resizeStart", this.posData);
    },
    // 缩放控件的mousemove事件回调函数
    stickMove(ev) {
      // 当前空间类型
      let currentStick = this.currentStick;
      // 起始位置信息
      const {mx, my, top, left, bottom, right} = this.bodyStartPos;

      // 位移向量
      const vector = {x: ev.clientX - mx, y: ev.clientY - my};

      // 如果比例锁定，将非m控件替代为m控件计算
      if (this.lock && !currentStick.match("m")) {
        currentStick = `${currentStick[0]}m`;
      }
      const rad = this.rotate * (Math.PI / 180);
      const x = vector.x * Math.cos(rad) + vector.y * Math.sin(rad);
      const y = vector.y * Math.cos(rad) - vector.x * Math.sin(rad);
      // 更新位置信息
      let scale = this.getScale();

      // 根据当前控件类型更新位置信息
      currentStick[0] == "t" && (this.top = top + y * scale);
      currentStick[0] == "b" && (this.bottom = bottom - y * scale);
      currentStick[1] == "l" && (this.left = left + x * scale);
      currentStick[1] == "r" && (this.right = right - x * scale);

      // 触发缩放事件
      this.$emit("resizing", this.posData);
    },

    // 旋转控件的mousedown事件回调函数
    rotateDown(ev) {
      if (!this.activeable) return;
      // 获取当前元素位置大小信息，用于计算旋转元素的中心点
      const vdr = this.$refs.vdr;
      const rect = vdr.getBoundingClientRect();
      const {left, top, width, height} = rect;

      this.rotateDrag = true;
      this.currentStick = "angle";

      // 开始点
      pointB = {X: ev.clientX, Y: ev.clientY};
      // 中点
      pointA = {X: left + width / 2, Y: top + height / 2};
      // 触发事件
      this.$emit("rotateStart", this.posData);
    },

    // 旋转控件的mousemove事件回调函数
    rotateMove(ev) {
      // 记录结束点
      pointC = {X: ev.clientX, Y: ev.clientY};
      // AB、AC向量
      const AB = {X: pointB.X - pointA.X, Y: pointB.Y - pointA.Y};
      const AC = {X: pointC.X - pointA.X, Y: pointC.Y - pointA.Y};

      // AB与AC叉乘，根据右手定则：direct小于零逆时针旋转，大于零顺时针旋转
      const direct = AB.X * AC.Y - AB.Y * AC.X;

      // AB、AC向量的模
      const AB_dx = pointA.X - pointB.X;
      const AC_dx = pointA.X - pointC.X;
      const AB_dy = pointA.Y - pointB.Y;
      const AC_dy = pointA.Y - pointC.Y;
      const lengthAB = Math.sqrt(AB_dx * AB_dx + AB_dy * AB_dy);
      const lengthAC = Math.sqrt(AC_dx * AC_dx + AC_dy * AC_dy);

      // 向量点乘，公式： A*B = x1*x2 + y1*y2
      const product = AB.X * AC.X + AB.Y * AC.Y;

      // 两个向量之间的夹角的计算公式 ：a * b = |a| * |b| * cosθ
      // 公式转换 θ = Math.acos(a * b /( |a| * |b| )); （θ为弧度）
      // Math.acos的参数范围[-1, 1] ,返回值[-PI, PI],其余值返回 NAN
      const rad = Math.acos(product / (lengthAB * lengthAC));
      const angle = (rad / Math.PI) * 180 || 0;

      // 根据旋转方向加减角度
      this.rotate = direct < 0 ? this.rotate - angle : this.rotate + angle;

      // 触发事件
      this.$emit("rotating", this.posData);

      // 更新起点
      pointB = {X: ev.clientX, Y: ev.clientY};
    },

    // mousemove事件回调函数
    move(ev) {
      if (this.draggable && this.bodyDrag) {
        // const { clientX, clientY } = ev;
        this.bodyMove(ev);
      }
      if (this.resizeable && this.stickDrag) {
        this.stickMove(ev);
      }
      if (this.rotateable && this.rotateDrag) {
        this.rotateMove(ev);
      }
    },
    // mousemup事件回调函数
    up() {
      this.moving = false
      // 拖拽停止
      if (this.draggable && this.bodyDrag) {
        this.bodyDrag = false;
        // 更新宽高比例，当宽高其中一个为0时，不更新比例
        if (this.width > 0 && this.height > 0) {
          this.whRatio = this.width / this.height;
        }
        const temArr = new Array(3).fill({display: false, position: '', origin: '', lineLength: ''})
        const refLine = {vLine: [], hLine: []}
        for (let i in refLine) {
          refLine[i] = JSON.parse(JSON.stringify(temArr))
        }
        this.$emit('refLineParams', refLine)
        this.removeVdrs();
        this.$emit("dragStop", this.posData);
      }
      // 缩放停止
      if (this.resizeable && this.stickDrag) {
        this.stickDrag = false;
        this.$emit("resizeStop", this.posData);
      }
      // 旋转停止
      if (this.rotateable && this.rotateDrag) {
        this.rotateDrag = false;
        this.$emit("rotateStop", this.posData);
      }
    },
    // 拖拽对齐辅助线
    async snapCheck() {
      let width = this.width
      let height = this.height
      let activeLeft = this.left
      let activeRight = this.left + width
      let activeTop = this.top
      let activeBottom = this.top + height
      // 初始化辅助线数据
      const temArr = new Array(3).fill({display: false, position: '', origin: '', lineLength: ''})
      const refLine = {vLine: [], hLine: []}
      for (let i in refLine) {
        refLine[i] = JSON.parse(JSON.stringify(temArr))
      }
      // 获取当前父节点下所有子节点
      const nodes = this.$el.parentNode.childNodes;
      let tem = {
        value: {x: [[], [], []], y: [[], [], []]},
        display: [],
        position: []
      }
      const {groupWidth, groupHeight, groupLeft, groupTop, bln} = await this.getActiveAll(nodes)
      if (!bln) {
        width = groupWidth
        height = groupHeight
        activeLeft = groupLeft
        activeRight = groupLeft + groupWidth
        activeTop = groupTop
        activeBottom = groupTop + groupHeight
      }
      for (let item of nodes) {
        if (item.className !== undefined && !item.className.includes('vdr-active') && item.className.includes('vdr')) {
          const w = item.offsetWidth
          const h = item.offsetHeight
          const [l, t] = this.formatTransformVal(item)
          const r = l + w // 对齐目标right
          const b = t + h // 对齐目标的bottom
          const hc = Math.abs((activeTop + height / 2) - (t + h / 2)) <= this.snapTolerance // 水平中线
          const vc = Math.abs((activeLeft + width / 2) - (l + w / 2)) <= this.snapTolerance // 垂直中线
          const ts = Math.abs(t - activeBottom) <= this.snapTolerance // 从上到下
          const TS = Math.abs(b - activeBottom) <= this.snapTolerance // 从上到下
          const bs = Math.abs(t - activeTop) <= this.snapTolerance // 从下到上
          const BS = Math.abs(b - activeTop) <= this.snapTolerance // 从下到上
          const ls = Math.abs(l - activeRight) <= this.snapTolerance // 外左
          const LS = Math.abs(r - activeRight) <= this.snapTolerance // 外左
          const rs = Math.abs(l - activeLeft) <= this.snapTolerance // 外右
          const RS = Math.abs(r - activeLeft) <= this.snapTolerance // 外右

          tem['display'] = [ts, TS, bs, BS, hc, hc, ls, LS, rs, RS, vc, vc]
          tem['position'] = [t, b, t, b, t + h / 2, t + h / 2, l, r, l, r, l + w / 2, l + w / 2]
          if (ts) {
            if (bln) {
              this.top = t - height
              this.bottom = this.parentHeight - this.top - height
            }
            tem.value.y[0].push(l, r, activeLeft, activeRight)
          }
          if (bs) {
            if (bln) {
              this.top = t
              this.bottom = this.parentHeight - this.top - height
            }
            tem.value.y[0].push(l, r, activeLeft, activeRight)
          }
          if (TS) {
            if (bln) {
              this.top = b - height
              this.bottom = this.parentHeight - this.top - height
            }
            tem.value.y[1].push(l, r, activeLeft, activeRight)
          }
          if (BS) {
            if (bln) {
              this.top = b
              this.bottom = this.parentHeight - this.top - height
            }
            tem.value.y[1].push(l, r, activeLeft, activeRight)
          }
          if (ls) {
            if (bln) {
              this.left = l - width
              this.right = this.parentWidth - this.left - width
            }
            tem.value.x[0].push(t, b, activeTop, activeBottom)
          }
          if (rs) {
            if (bln) {
              this.left = l
              this.right = this.parentWidth - this.left - width
            }
            tem.value.x[0].push(t, b, activeTop, activeBottom)
          }
          if (LS) {
            if (bln) {
              this.left = r - width
              this.right = this.parentWidth - this.left - width
            }
            tem.value.x[1].push(t, b, activeTop, activeBottom)
          }
          if (RS) {
            if (bln) {
              this.left = r
              this.right = this.parentWidth - this.left - width
            }
            tem.value.x[1].push(t, b, activeTop, activeBottom)
          }
          if (hc) {
            if (bln) {
              this.top = t + h / 2 - height / 2
              this.bottom = this.parentHeight - this.top - height
            }
            tem.value.y[2].push(l, r, activeLeft, activeRight)
          }
          if (vc) {
            if (bln) {
              this.left = l + w / 2 - width / 2
              this.right = this.parentWidth - this.left - width
            }
            tem.value.x[2].push(t, b, activeTop, activeBottom)
          }
          // 辅助线坐标与是否显示(display)对应的数组,易于循环遍历
          const arrTem = [0, 1, 0, 1, 2, 2, 0, 1, 0, 1, 2, 2]
          for (let i = 0; i <= arrTem.length; i++) {
            // 前6为Y辅助线,后6为X辅助线
            const xory = i < 6 ? 'y' : 'x'
            const horv = i < 6 ? 'hLine' : 'vLine'
            if (tem.display[i]) {
              const {origin, length} = this.calcLineValues(tem.value[xory][arrTem[i]])
              refLine[horv][arrTem[i]].display = tem.display[i]
              refLine[horv][arrTem[i]].position = tem.position[i] + 'px'
              refLine[horv][arrTem[i]].origin = origin
              refLine[horv][arrTem[i]].lineLength = length
            }
          }
        }
      }
      this.$emit('refLineParams', refLine)
    },
    calcLineValues(arr) {
      const length = Math.max(...arr) - Math.min(...arr) + 'px'
      const origin = Math.min(...arr) + 'px'
      return {length, origin}
    },
    async getActiveAll(nodes) {
      const activeAll = []
      const XArray = []
      const YArray = []
      let groupWidth = 0
      let groupHeight = 0
      let groupLeft = 0
      let groupTop = 0
      for (let item of nodes) {
        if (item.className !== undefined && item.className.includes('vdr-active')) {
          activeAll.push(item)
        }
      }
      const AllLength = activeAll.length
      if (AllLength > 1) {
        let parentOffset = this.parentElement.getBoundingClientRect()
        for (let i of activeAll) {
          const offset = i.getBoundingClientRect()
          const l = offset.left - parentOffset.left
          const r = l + i.offsetWidth
          const t = offset.top - parentOffset.top
          const b = t + i.offsetHeight
          XArray.push(t, b)
          YArray.push(l, r)
        }
        groupWidth = Math.max(...YArray) - Math.min(...YArray)
        groupHeight = Math.max(...XArray) - Math.min(...XArray)
        groupLeft = Math.min(...YArray)
        groupTop = Math.min(...XArray)
      }
      const bln = AllLength === 1
      return {groupWidth, groupHeight, groupLeft, groupTop, bln}
    },
    // 正则获取left与top
    formatTransformVal(element) {
      const style = window.getComputedStyle(element)
      const matrix = style['transform'] || style.webkitTransform || style.mozTransform

      // No transform property. Simply return 0 values.
      if (matrix === 'none') {
        return [0, 0, 0]
      }

      // Can either be 2d or 3d transform
      const matrixType = matrix.includes('3d') ? '3d' : '2d'
      const matrixValues = matrix.match(/matrix.*\((.+)\)/)[1].split(', ')

      // 2d matrices have 6 values
      // Last 2 values are X and Y.
      // 2d matrices does not have Z value.
      if (matrixType === '2d') {
        return [+matrixValues[4], +matrixValues[5], 0]
      }

      // 3d matrices have 16 values
      // The 13th, 14th, and 15th values are X, Y, and Z
      if (matrixType === '3d') {
        return [+matrixValues[12], +matrixValues[13, +matrixValues[14]]]

      }
    },
    createVdrs() {
      let sketch = document.querySelector('.item-box');
      this.vdrs = []
      if (!sketch) {
        return
      }
      let others = sketch.querySelectorAll('.vdr')
      let element = [].filter.call(others, (el) => {
        return this.$el.parentNode !== el.parentNode && !el.classList.contains('dragdropbox')
      })
      element.unshift(sketch)

      let parentOffset = this.$el.parentNode.getBoundingClientRect()
      for (let i = 0; i < element.length; ++i) {
        let el = element[i]
        let box = el.getBoundingClientRect()
        let left = box.left - parentOffset.left
        let top = box.top - parentOffset.top
        let width = box.width
        let height = box.height;
        let vdr = document.createElement('div')
        vdr.className = 'vdr static'
        vdr.style.transform = `translate3d(${left}px,${top}px,0)`;
        vdr.style.height = height + 'px';
        vdr.style.width = width + 'px';
        this.$el.parentNode.prepend(vdr)
        this.vdrs.push(vdr)
      }
    },
    removeVdrs() {
      this.vdrs && this.vdrs.forEach(el => {
        el.remove()
      })
    }
  },
  watch: {
    left(value) {
      // body处于拖动状态时，不重新计算宽高
      if (this.bodyDrag) return;

      // 更新宽度、宽度范围校验
      if (value > this.minOffsetLeft) this.left = this.minOffsetLeft;
      if (value < this.maxOffsetLeft) this.left = this.maxOffsetLeft;
      this.width = this.parentWidth - this.left - this.right;

      // 处理锁定状态
      if (this.lock) {
        this.height = this.width / this.whRatio;
      }
    },
    top(value) {
      // body处于拖动状态时，不重新计算宽高
      if (this.bodyDrag) return;

      // 更新高度、最小高度检测
      if (value > this.minOffsetTop) this.top = this.minOffsetTop;
      if (value < this.maxOffsetTop) this.top = this.maxOffsetTop;
      this.height = this.parentHeight - this.top - this.bottom;

      // 处理锁定状态
      if (this.lock) {
        this.width = this.height * this.whRatio;
        // 如果操作的是左边缩放控件，则重新计算left，以右边为参照
        if (this.currentStick.match("l")) {
          this.left = this.parentWidth - this.right - this.width;
        }
      }
    },
    right(value) {
      // body处于拖动状态时，不重新计算宽高
      if (this.bodyDrag) return;

      // 更新宽度、宽度范围校验
      let width = this.parentWidth - value - this.left;
      width = width < this.minWidth ? this.minWidth : width;
      width = width > this.maxWidth ? this.maxWidth : width;
      this.width = width;

      if (this.lock) {
        this.height = this.width / this.whRatio;
      }
    },
    bottom(value) {
      // body处于拖动状态时，不重新计算宽高
      if (this.bodyDrag) return;

      // 更新高度、最小高度检测
      let height = this.parentHeight - value - this.top;
      height = height < this.minHeight ? this.minHeight : height;
      height = height > this.maxHeight ? this.maxHeight : height;
      this.height = height;
      // 处理锁定状态
      if (this.lock) {
        this.width = this.height * this.whRatio;
        // 如果操作的是左边缩放控件，则重新计算left，以右边为参照
        if (this.currentStick.match("l")) {
          this.left = this.parentWidth - this.right - this.width;
        }
      }
    },
    w(value) {
      if (value <= 0 || isNaN(value) || value == null) {
        this.width = 0;
        this.height = 0;
        return;
      }
      this.width = value;
      if (this.lock) {
        this.height = Math.round(value / (this.w / this.h));
      }
      this.whRatio = this.width / this.height;
    },
    h(value) {
      if (value <= 0 || isNaN(value) || value == null) {
        this.width = 0;
        this.height = 0;
        return;
      }
      this.height = value;
      if (this.lock) {
        this.width = Math.round(value * (this.w / this.h));
      }
      this.whRatio = this.width / this.height;
    },
    x(value) {
      this.left = value;
      this.right = this.parentWidth - this.w - value;
    },
    y(value) {
      this.top = value;
      this.bottom = this.parentHeight - this.h - value;
    },
    z(value) {
      this.zIndex = value;
    },
    r(value) {
      this.rotate = value;
    },
    lock(value) {
      if (value) {
        this.whRatio = this.width / this.height;
        this.setLimitValue();
      }
    }
  }
};
</script>
