const CONFIG = {
  x: 0,
  y: 0,
  scale: 1,
  maxScale: 3,
  minScale: 0.5,
  moving: true,
  speed: 0.2,
  origin: '0,0,0'
};
var _fn;

export class Panmove {
  constructor(config) {
    this.$config = Object.assign({}, CONFIG, config);
    let {zoomDom, eventDom} = this.$config
    this.zoomDom = zoomDom
    this.eventDom = eventDom
    this.zoomDom.style.transform = 'matrix(1,0,0,1,0,0)';
    this.center()
    this.lastPos();
  }

  center() {
    this.$config.x = this.eventDom.offsetWidth / 2 - this.zoomDom.offsetWidth / 2
    this.$config.y = this.$config.y || 30
    this.moveTo(this.$config.scale, {
      x: this.$config.x,
      y: this.$config.y
    })
  }

  cal(e) {
    var self = this;
    var evtDom = self.eventDom;
    var u = self.$config.x;
    var v = self.$config.y;
    var lastScale = self.$config.scale;
    var scale = e.wheelDelta <= 0 ? lastScale - self.$config.speed : lastScale + self.$config.speed;
    scale = Number(scale.toFixed(2))
    if (scale >= self.$config.maxScale || scale <= self.$config.minScale) {
      return false
    }
    var scaleRate = Number(parseFloat(scale / lastScale).toFixed(2))
    if (scale <= 0.1) {
      return false
    }
    var x0 = e.clientX - self.getRect(evtDom).left;
    var y0 = e.clientY - self.getRect(evtDom).top;

    var offsetX = Number(parseFloat(scaleRate * u + (1 - scaleRate) * x0).toFixed(2));
    var offsetY = Number(parseFloat(scaleRate * v + (1 - scaleRate) * y0).toFixed(2));
    self.$config.x = offsetX
    self.$config.y = offsetY
    self.$config.scale = scale
    self.matrixTo();
    self.lastPos();
    self.$config.cal && self.$config.cal(self.$config)
    return self.$config.scale
  }

  getRect(dom) {
    return dom.getBoundingClientRect()
  }

  zoomIn(e) {
    this.cal({
      wheelDelta: 0,
      clientX: this.getRect(this.eventDom).width / 2,
      clientY: this.getRect(this.eventDom).height / 2
    });
  }

  zoomOut(e) {
    this.cal({
      wheelDelta: 1,
      clientX: this.getRect(this.eventDom).width / 2,
      clientY: this.getRect(this.eventDom).height / 2
    });
  }

  zoom(e) {
    return this.cal(e)
  }


  moveTo(scale, position) {
    this.$config.scale = scale ? scale : this.$config.scale;
    this.$config.x = position.x ? position.x : this.$config.x;
    this.$config.y = position.y ? position.y : this.$config.y;
    this.matrixTo();
  }

  move(e) {
    var self = this;
    if (!self.canMove) {
      return
    }
    self.$config.x = self.$oldPos.x + e.clientX - self.moveStart.clientX;
    self.$config.y = self.$oldPos.y + e.clientY - self.moveStart.clientY;
    this.matrixTo();
    self.$config.move && self.$config.move(e)
  }

  matrixTo() {
    var self = this;
    self.zoomDom.style.transform = _fn.getMatrix(
      {
        x: self.$config.scale,
        y: self.$config.scale
      },
      {
        x: self.$config.x,
        y: self.$config.y
      })
  }


  addMask() {
    var self = this;
    if (self.maskLoad) {
      return
    }
    if (!self.mask) {
      self.mask = document.createElement('div');
      self.mask.style.zIndex = 999
      self.mask.style.position = 'absolute'
      self.mask.style.top = 0
      self.mask.style.right = 0
      self.mask.style.left = 0
      self.mask.style.bottom = 0
      self.mask.addEventListener('mousedown', (e) => {
        if (e.button === 0) {
          e.stopPropagation();
          self.getStartPos(e);
          self.canMove = true
        }
      })
    }
    self.eventDom.append(self.mask);
    self.maskLoad = true;
  }

  removeMask() {
    var self = this;
    self.mask && self.mask.remove();
    self.maskLoad = false;
  }

  getStartPos(e) {
    this.moveStart = e;
  }

  lastPos(e) {

    var self = this;
    var old = this.zoomDom.style.transform.replace(/^matrix(3d)?\((.*)\)$/, '$2').split(/, /);
    this.$oldPos = {
      x: parseFloat(old[4], 3),
      y: parseFloat(old[5], 3),
      scale: parseFloat(old[0], 3)
    };
    self.canMove = false
    e && self.$config.moveStop && self.$config.moveStop(e)
  }
}

_fn = {
  // isIn: function ($div, e) {
  //   var x = e.clientX;
  //   var y = e.clientY;
  //   var div_x_1 = $div.offset().left;
  //   var div_y_1 = $div.offset().top;
  //   var div_x_2 = $div.offset().left + $div.outerWidth();
  //   var div_y_2 = $div.offset().top + $div.outerHeight();
  //   if (x < div_x_1 || x > div_x_2 || y < div_y_1 || y > div_y_2) {
  //     return false
  //   } else {
  //     return true
  //   }
  // },
  getMatrix: function (scale, position) {
    //var old = this.zoomDom.css('transform').replace(/^matrix(3d)?\((.*)\)$/, '$2').split(/, /);
    //this.$config.x = parseFloat(old[4], 3);
    //this.$config.y = parseFloat(old[5], 3);
    //this.$config.scale = parseFloat(old[0], 3);
    var a = 1, b = 0, c = 0, d = 1, e = 0, f = 0;
    var sx = scale.x ? scale.x : 1;
    var sy = scale.y ? scale.y : 1;
    var x = position.x ? position.x : 0;
    var y = position.y ? position.y : 0;

    sx = Number(sx);
    sy = Number(sy);
    x = Number(x);
    y = Number(y);

    if (isNaN(sx) || isNaN(sy) || isNaN(x) || isNaN(y)) {
      console.warn('isNaN with scale');
      return 'matrix(1,0,0,1,0,0)'
    }
    return 'matrix(' + sx * a + ',0,0,' + sy * d + ',' + (e + x) + ',' + (f + y) + ')'
  }
};
