import {hasClass, isArray, isPromise} from "@/dragBase/utils/utils";

export default {
  data() {
    return {
      shiftKey: false
    }
  },
  methods: {
    lock({isLock = false}) {
      this.shiftKey = isLock
    },
    select({id}) {
      this.$fire('select', {event: {ctrlKey: false}, id: id}, false)
    },
    move({direction}) {
      let selected = isArray(this.selected) ? this.selected : [this.selected]
      let resizeable = this.$refs.resizeable[0]
      if (!resizeable || !selected) {
        return
      }
      selected.forEach(el => {
        let {left, top} = el
        if (direction == 'up') {
          top -= 1
        } else if (direction == 'down') {
          top += 1
        } else if (direction == 'left') {
          left -= 1
        } else if (direction == 'right') {
          left += 1
        }
        this.$fire('updateItem', {id: el.id, data: {left: parseFloat(left.toFixed(2)), top: parseFloat(top.toFixed(2))}})
      })
      this.$fire('move', direction, true)
    },
    copy() {
      let item = this.selected;
      if (item.length > 0) {
        item = JSON.parse(JSON.stringify(item))
        var promise = this.beforeCopy(item);
        if (isPromise(promise)) {
          promise.then(res => {
            this.$fire('copySelected', res, false)

          })
        } else {
          this.$fire('copySelected', promise, false)
        }
      }
    },
    paste() {
      this.$fire('paste')
    },
    undo() {
      this.$fire('undo')
    },
    redo() {
      // this.$fire('redo')
    },
    group() {
      this.$fire('group', null, true)
    },
    breakGroup() {
      this.$fire('breakGroup', null, true)
    },
    deleteSelect() {
      if (this.selected.length > 0 && this.beforeDelete(this.selected)) {
        this.$fire('removeSelected', null, true)
      }
    },
    zIndex({type, id}) {
      this.$fire(type, {id}, true)
    },
    cleanSelect({e}) {
      if (e && hasClass(e.target, 'vdr')) {
        return
      }
      if (this.selected.length <= 0) {
        return
      }
      this.$fire('cleanSelect')
    },
    initTree({data, snapShot = true}) {
      this.$fire('initTree', data, snapShot)
    },
    updateItem({id, data, snapShot = true}) {
      this.$fire('updateItem', {id, data}, snapShot)
    },
    addToSketch({parent = null, item = [], snapShot = true}) {
      if (item.length > 0) {
        this.$fire('addToSketch', {parent, item}, snapShot)
      }
    }
  },

}