import Vue from 'vue'
import { getAxis, isArray, uuid } from "@/dragBase/utils/utils";

const groupBox = {
  type: 'dragdropbox'
}

/**
 * 初始化数据
 * @param state
 * @param item
 * @param parent
 * @returns {{children}}
 */
function initAddData(state, item, parent = {id: null}) {
  item.id = uuid()
  item.parentId = parent.id
  item = {
    left: 0,
    top: 0,
    width: 50,
    height: 50,
    ...item,
    active: false,
    childActiveAble: false
  }
  if (item.children && item.children.length) {
    for ( let k = 0; k < item.children.length; ++k ) {
      item.children[ k ] = initAddData(state, item.children[ k ], item)
    }
  } else {
    item.children = []
  }
  state.sketchupMap[ item.id ] = item;
  return item
}

/**
 * 取消激活子集
 * @returns {*[]}
 * @param item
 */
function deActiveChild(item = null) {
  if (!item) {
    return
  }
  item.active = false;
  item.childActiveAble = false;
  for ( let i = 0; i < item.children.length; ++i ) {
    item.children[ i ] = deActiveChild(item.children[ i ])
  }
  return item
}


/**
 * 整理编组数据
 * @param state
 * @param select 选中数据
 * @param box 父级
 * @param children 插入盒子
 * @param item 插入项
 * @param coord 坐标
 */
function groupData(state, select, box, children, item, coord) {
  select.forEach(el => {
    state.sketchupMap[ el.id ].left = state.sketchupMap[ el.id ].left - coord.left;
    state.sketchupMap[ el.id ].top = state.sketchupMap[ el.id ].top - coord.top;
    state.sketchupMap[ el.id ].parentId = item.id
    children.push(state.sketchupMap[ el.id ])
    let index = box.findIndex(ele => {
      return el.id === ele.id
    })
    box.splice(index, 1)
  })
  box.push(item)
}

/**
 * 打散编组
 * @param state
 * @param select
 * @param box
 * @param coord
 * @param parent
 */
function breakGroupData(state, select, box, coord, parent) {
  select.children.map(el => {
    state.sketchupMap[ el.id ].parentId = parent;
    state.sketchupMap[ el.id ].top = state.sketchupMap[ el.id ].top + coord.top;
    state.sketchupMap[ el.id ].left = state.sketchupMap[ el.id ].left + coord.left;
    box.push(state.sketchupMap[ el.id ])
  })
  let index = box.findIndex(el => {
    return el.id === select.id
  })
  box.splice(index, 1)
  delete state.sketchupMap[ select.id ]
}

/**
 * 更具路径更新对象值
 * @param obj
 * @param path
 * @param value
 * @param vm
 */
function updateValueByPath(obj, path, value, vm) {
  var tempObj = obj;
  path = path.replace(/\[(\w+)\]/g, '.$1');
  path = path.replace(/^\./, '');
  var keyArr = path.split('.');
  var i = 0;
  for ( var len = keyArr.length; i < len - 1; ++i ) {
    var key = keyArr[ i ];
    if (key in tempObj) {
      tempObj = tempObj[ key ];
    }
  }
  if (value && value.constructor === Object) {
    for ( let key2 in value ) {
      let val = value[ key2 ];
      vm.$set(tempObj, keyArr[ i ], tempObj[ keyArr[ i ] ] || {})
      updateValueByPath(tempObj[ keyArr[ i ] ], key2, val, vm);
    }
  } else {
    vm.$set(tempObj, keyArr[ i ], value)
  }
}

/**
 * 更具路径更获取对象值
 * @param obj
 * @param path
 * @param value
 */
function getValueByPath(obj, path, value) {
  var tempObj = obj;
  path = path.replace(/\[(\w+)\]/g, '.$1');
  path = path.replace(/^\./, '');
  var keyArr = path.split('.');
  var i = 0;
  for ( var len = keyArr.length; i < len - 1; ++i ) {
    var key = keyArr[ i ];
    if (key in tempObj) {
      tempObj = tempObj[ key ];
    }
  }
  return JSON.parse(JSON.stringify(tempObj[ keyArr[ i ] ] || {}));
}


/**
 * 选中
 * @param event
 * @param id
 */
function select({event, id}) {
  unselect.call(this, {event, id})
  let state = this.state;
  let selected = state.selected;
  //重置 当先选中子集
  deActiveChild(state.sketchupMap[ id ])
  //激活当前
  state.sketchupMap[ id ].active = true;
  if (event.ctrlKey) {
    //多选
    selected.push(state.sketchupMap[ id ])
  } else {
    //单选
    selected = [state.sketchupMap[ id ]]
  }
  state.selected = selected;
  this.$emit('selected', state.selected)
  Vue.set(state, 'sketchup', state.sketchup)
}

/**
 * 取消选中到el.parent
 * @param event
 * @param id
 */
function unselect({event, id = null}) {
  let state = this.state;
  let selected = state.selected;
  let el = state.sketchupMap[ id ] || {}
  for ( let i = 0; i < selected.length; i++ ) {
    let ele = selected[ i ]
    if (ele.id === el.parentId) {
      continue
    }
    if (!state.sketchupMap[ ele.id ]) continue
    let parentId = state.sketchupMap[ ele.id ].parentId;
    let out = false;
    while ( parentId ) {
      if (parentId !== el.parentId) {
        deActiveChild(state.sketchupMap[ parentId ])
        parentId = state.sketchupMap[ parentId ].parentId;
        out = true
      } else {
        if (!event.ctrlKey) {
          deActiveChild(state.sketchupMap[ ele.id ])
          out = true;
        }
        break
      }
    }
    if (out) {
      selected.splice(i, 1)
      i--;
    }
  }
  state.selected = selected
}

function dealMap(state, sketchup) {
  let id = sketchup.id;
  state.sketchupMap[ id ] = sketchup
  if (sketchup.children && sketchup.children.length) {
    for ( let i = 0; i < sketchup.children.length; ++i ) {
      dealMap(state, sketchup.children[ i ])
    }
  }
}

class Bus {
  constructor() {
    if (!this.init) {
      this.init = true;
      this.bus = new Vue({
        computed: {
          selected() {
            return JSON.parse(JSON.stringify(this.state.selected))
          }
        },
        data() {
          return {
            state: {
              sketchup: {},//画布数据
              sketchupMap: {},//画布数据
              selected: [],//选中项
              copyData: null,
              snapshotStack: [],
              snapshotLength: 30,
              snapshotIndex: -1
            }
          }
        },
        created() {
          /**
           * 添加到画布
           * @param item
           * @param index
           */
          this.$on('addToSketch', ({parent = null, item, index = -1}) => {
            let state = this.state;
            let ids = []
            parent = parent ? parent : state.sketchup;
            if (isArray(item)) {
              item.forEach(ele => {
                ele = JSON.parse(JSON.stringify(ele))
                if (parent.type === groupBox.type) {
                  ele = initAddData(state, ele, parent)
                  ids.push(ele.id)
                  if (index === -1) {
                    state.sketchupMap[ parent.id ].children.push(ele)
                  } else {
                    state.sketchupMap[ parent.id ].children.splice(index, 0, ele)
                  }
                } else {
                  ele = initAddData(state, ele, state.sketchup)
                  ids.push(ele.id)
                  if (index === -1) {
                    state.sketchup.children.push(ele)
                  } else {
                    state.sketchup.children.splice(index, 0, ele)
                  }
                }
              })
            } else {
              item = JSON.parse(JSON.stringify(item))
              item = initAddData(state, item, parent)
              ids.push(item.id)
              if (parent) {
                if (index === -1) {
                  state.sketchupMap[ parent.id ].push(item)
                } else {
                  state.sketchupMap[ parent.id ].splice(index, 0, item)
                }
              } else {
                if (index === -1) {
                  state.sketchup.children.push(item)
                } else {
                  state.sketchup.children.splice(index, 0, item)
                }
              }
            }
            this.$nextTick(() => {
              this.$emit('updateGroup')
              this.$emit('addend', ids)
            })
          })
          
          /**
           * 画布组件选中
           * @param el
           */
          this.$on('select', ({event = {}, id = null}) => {
            let state = this.state;
            if (!id) return
            let el = state.sketchupMap[ id ]
            //判断是否已经选中了
            let exist = -1;
            exist = state.selected.findIndex(ele => {
              return ele.id == el.id
            })
            if (( exist > -1 && event.ctrlKey ) || exist < 0) {
              select.call(this, {event: event, id})
            }
          })
          
          /**
           * 取消选中
           */
          this.$on('unSelect', ({event = {}, id}) => {
            //退栈到 同el.parentId，每一占一定是同父级
            let state = this.state;
            unselect.call(this, {event, id});
            this.$emit('selected', state.selected)
            Vue.set(state, 'sketchup', state.sketchup)
          })
          
          /**
           * 清空选项
           */
          this.$on('cleanSelect', () => {
            let state = this.state;
            let i = state.selected.length - 1;
            while ( i >= 0 ) {
              let item = state.selected[ i ]
              if (!item || !item.parentId) {
                break
              }
              unselect.call(this, {event: {ctrlKey: false}, id: null})
              --i;
            }
            state.selected = [];
            this.$emit('selected', state.selected)
            Vue.set(state, 'sketchup', state.sketchup)
          })
          
          /**
           * 更新选画布中项
           * @param data
           */
          this.$on('updateSelected', (data = null) => {
            let state = this.state;
            //只考虑单选 更新stack 最后一个
            if (!data) {
              return
            }
            if (data && Object.keys(data).length === 0) {
              return
            }
            let selected = state.selected[ state.selected.length - 1 ] || null
            if (!selected || selected.length > 1) {
              return
            }
            let updates = {
              ids: selected.id,
              data: {}
            }
            for ( let keypath in data ) {
              updates.data[ keypath ] = getValueByPath(state.sketchupMap[ selected.id ], keypath);
              updateValueByPath(state.sketchupMap[ selected.id ], keypath, data[ keypath ], this)
            }
            Vue.set(state, 'sketchup', state.sketchup)
          })
          
          /**
           * 从画布删除
           */
          this.$on('removeSelected', () => {
            let state = this.state;
            let selected = state.selected;
            let update = false;
            let parent, index, map = state.sketchupMap;
            if (isArray(selected) && selected.length) {
              //删除多个
              update = true
              selected.map((el) => {
                parent = map[ map[ el.id ].parentId ];
                this.$emit('unSelect', {id: el.id});
                if (!parent) {
                  index = state.sketchup.children.findIndex(ele => {
                    return ele.id === el.id
                  })
                  state.sketchup.children.splice(index, 1);
                } else {
                  index = parent.children.findIndex(ele => {
                    return ele.id === el.id
                  })
                  parent.children.splice(index, 1);
                }
                state.sketchupMap[ el.id ].children.map(el => {
                  delete state.sketchupMap[ el.id ]
                })
                delete state.sketchupMap[ el.id ]
              })
            }
            this.$emit('updateGroup')
            update && Vue.set(state, 'sketchup', state.sketchup)
          })
          
          /**
           * 复制选中组件
           * @param data
           */
          this.$on('copySelected', (data) => {
            this.state.copyData = JSON.parse(JSON.stringify(data));
          })
          
          /**
           * 粘贴组件
           */
          this.$on('paste', () => {
            let state = this.state;
            if (!state.copyData) {
              return
            }
            this.$emit('recordShot')
            let items = JSON.parse(JSON.stringify(state.copyData));
            let ctrlKey = items.length > 0;
            let parent = state.selected[ 0 ] ? state.selected[ 0 ].parentId : null;
            items.forEach(item => {
              item.left += 10;
              item.top += 10;
              item = initAddData(state, item, {id: parent})
              if (parent) {
                state.sketchupMap[ parent ].children.push(item);
              } else {
                state.sketchup.children.push(item)
              }
            })
            
            let selected = JSON.parse(JSON.stringify(state.selected))
            selected.forEach(el => {
              this.$emit('unSelect', {id: el.id, event: {ctrlKey: false}})
            })
            items.forEach(item => {
              this.$emit('select', {id: item.id, event: {ctrlKey: ctrlKey}})
            })
            state.copyData = JSON.parse(JSON.stringify(items))
            this.$emit('updateGroup')
            
            // setTimeout(() => {
            //   selected.forEach(el => {
            //     this.$emit('unSelect', {el: el, event: {ctrlKey: ctrlKey}})
            //   })
            //   state.copyData = JSON.parse(JSON.stringify(items))
            //
            // }, 1)
          })
          /**
           * 编组
           * @param evt
           */
          this.$on('group', (evt = {}) => {
            let state = this.state;
            /*编组 select 一定是一个数组才会去编组*/
            evt && evt.stopPropagation && evt.stopPropagation();
            evt && evt.preventDefault && evt.preventDefault();
            let select = state.selected;
            if (isArray(select) && select.length > 1) {
              let parentId = state.selected[ 0 ].parentId;
              let box = JSON.parse(JSON.stringify(groupBox))
              let coord = getAxis(select)
              box = {...box, ...coord}
              box = initAddData(state, box, {id: parentId});
              if (parentId) {
                groupData(state, select, state.sketchupMap[ parentId ].children, state.sketchupMap[ box.id ].children, box, coord)
              } else {
                groupData(state, select, state.sketchup.children, state.sketchupMap[ box.id ].children, box, coord)
              }
              this.$emit('unSelect', {id: box.id})
              this.$emit('select', {id: box.id})
              Vue.set(state, 'sketchup', state.sketchup)
            }
          })
          
          /**
           * 编组打散
           */
          this.$on('breakGroup', () => {
            let state = this.state;
            //只有 type==groupBox.type 才能被打散
            let select = state.selected;
            if (!select || select.length === 0 || Object.keys(select).length === 0) {
              return
            }
            let coord
            if (isArray(select)) {
              select.map(el => {
                let {left, top, parentId} = el;
                let parent = parentId
                let box = state.sketchupMap[ parent ] ? state.sketchupMap[ parent ].children : state.sketchup.children
                coord = {left, top}
                if (el.type === groupBox.type && el.children.length) {
                  this.$emit('unSelect', {event: {ctrlKey: false}, id: el.id})
                  breakGroupData(state, el, box, coord, parent)
                  el.children.map(child => {
                    this.$emit('unSelect', {event: {ctrlKey: true}, id: child.id})
                    this.$emit('select', {event: {ctrlKey: true}, id: child.id})
                  })
                }
              })
              
            } else {
              let {left, top, parentId} = select;
              let parent = parentId
              let box = state.sketchupMap[ parent ] ? state.sketchupMap[ parent ].children : state.sketchup.children
              coord = {left, top}
              if (select.type === groupBox.type && select.children.length) {
                breakGroupData(state, select, box, coord, parent)
                this.$emit('cleanSelect')
                select.children.map(el => {
                  this.$emit('unSelect', {event: {ctrlKey: true}, id: el.id})
                  this.$emit('select', {event: {ctrlKey: true}, id: el.id})
                })
              }
            }
          })
          
          /**
           * 更新容器属性
           * @param state
           * @param id
           * @param coord
           */
          this.$on('updateGroupBox', ({id, coord}) => {
            let state = this.state;
            if (!state.sketchupMap[ id ]) {
              return
            }
            state.sketchupMap[ id ].left = state.sketchupMap[ id ].left + coord.left
            state.sketchupMap[ id ].top = state.sketchupMap[ id ].top + coord.top
            state.sketchupMap[ id ].width = coord.width
            state.sketchupMap[ id ].height = coord.height
            state.sketchupMap[ id ].children.map(ele => {
              state.sketchupMap[ ele.id ].left = state.sketchupMap[ ele.id ].left - coord.left;
              state.sketchupMap[ ele.id ].top = state.sketchupMap[ ele.id ].top - coord.top;
            })
          })
          /**
           * 初始化数据 主要是根节点 type 随意
           */
          this.$on('initTree', (tree) => {
            let state = this.state;
            state.sketchupMap = {}
            state.sketchup = {}
            state.selected = []
            state.copyData = null
            
            tree.type = 'sketchup'
            tree = JSON.parse(JSON.stringify(tree))
            tree = initAddData(state, tree, {});
            state.sketchup = tree;
          })
          
          /**
           * 跟新指定item数据
           * */
          this.$on('updateItem', ({id, data = null, updateGroupBox = true}) => {
            let state = this.state;
            //只考虑单选 更新stack 最后一个
            if (!data || !state.sketchupMap[ id ]) {
              return
            }
            let updates = {
              ids: id,
              data: {}
            }
            for ( let keypath in data ) {
              if (updateGroupBox) {
                updates.data[ keypath ] = getValueByPath(state.sketchupMap[ id ], keypath);
              }
              updateValueByPath(state.sketchupMap[ id ], keypath, data[ keypath ], this)
            }
            updateGroupBox && this.$emit('updateGroup')
            Vue.set(state, 'sketchup', state.sketchup)
          })
          
          /**
           * 层级移动
           * */
          this.$on('bringForward', ({id}) => {
            let state = this.state;
            let el = state.sketchupMap[ id ]
            if (!el) return
            let parent = state.sketchupMap[ el.parentId ]
            let index = parent.children.findIndex(ele => {
              return ele.id === id;
            })
            if (index !== parent.children.length - 1) {
              state.sketchupMap[ el.parentId ].children[ index ] = state.sketchupMap[ el.parentId ].children.splice(index + 1, 1, state.sketchupMap[ el.parentId ].children[ index ])[ 0 ]
            }
          })
          this.$on('sendBackward', ({id}) => {
            let state = this.state;
            let el = state.sketchupMap[ id ]
            if (!el) return
            let parent = state.sketchupMap[ el.parentId ]
            let index = parent.children.findIndex(ele => {
              return ele.id === id;
            })
            if (index !== 0) {
              state.sketchupMap[ el.parentId ].children[ index ] = state.sketchupMap[ el.parentId ].children.splice(index - 1, 1, state.sketchupMap[ el.parentId ].children[ index ])[ 0 ]
            }
          })
          this.$on('bringToFront', ({id}) => {
            let state = this.state;
            let el = state.sketchupMap[ id ]
            if (!el) return
            let parent = state.sketchupMap[ el.parentId ]
            let index = parent.children.findIndex(ele => {
              return ele.id === id;
            })
            if (index !== parent.children.length - 1) {
              state.sketchupMap[ el.parentId ].children.push(state.sketchupMap[ el.parentId ].children.splice(index, 1)[ 0 ])
            }
          })
          this.$on('sendToBack', ({id}) => {
            let state = this.state;
            let el = state.sketchupMap[ id ]
            if (!el) return
            let parent = state.sketchupMap[ el.parentId ]
            let index = parent.children.findIndex(ele => {
              return ele.id === id;
            })
            if (index !== 0) {
              state.sketchupMap[ el.parentId ].children.unshift(state.sketchupMap[ el.parentId ].children.splice(index, 1)[ 0 ])
            }
          })
          
          /*
          * undo redo
          * */
          this.$on('recordShot', () => {
            this.state.snapshotStack[ ++this.state.snapshotIndex ] = {
              select: JSON.parse(JSON.stringify(this.state.selected)),
              data: JSON.parse(JSON.stringify(this.state.sketchup))
            }
            if (this.state.snapshotStack.length > this.state.snapshotLength) {
              this.state.snapshotStack.shift()
              this.state.snapshotIndex--;
            }
          })
          
          this.$on('undo', () => {
            if (this.state.snapshotIndex >= 0) {
              this.state.sketchupMap = {}
              let sketchup = this.state.snapshotStack[ this.state.snapshotIndex ]
              if (!sketchup) {
                return
              }
              this.state.snapshotIndex--;
              dealMap(this.state, sketchup.data)
              sketchup.select = sketchup.select || [];
              this.state.selected = []
              sketchup.select.forEach((el) => {
                this.state.selected.push(this.state.sketchupMap[ el.id ])
              })
              Vue.set(this.state, 'sketchup', sketchup.data)
            }
          })
          
          this.$on('redo', () => {
            if (this.state.snapshotIndex <= this.state.snapshotStack.length) {
              this.state.sketchupMap = {}
              this.state.snapshotIndex++;
              let sketchup = this.state.snapshotStack[ this.state.snapshotIndex ]
              if (!sketchup) {
                return
              }
              
              dealMap(this.state, sketchup.data)
              this.state.selected = sketchup.select;
              Vue.set(this.state, 'sketchup', sketchup.data)
            }
          })
          
        },
        methods: {
          updateConfig(config) {
            this.state.snapshotLength = config.snapshotLength ? config.snapshotLength : this.state.snapshotLength
          }
        }
      })
    }
    return this.bus
  }
  
}

export default new Bus()