<template>
  <div class="d-root" :style="renderStyle()">{{renderData}}</div>
</template>

<script>
export default {
  name: "droot",
  props: {
    renderData: {
      type: Object,
      required: true
    }
  },
  methods: {
    renderStyle() {
      let {renderData} = this;
      return {
        background:renderData.background
      }
    }
  }
}
</script>

<style scoped lang="scss">
.d-root {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

</style>