import ruler from './ruler'
import './pdruler.scss'
import {Panmove} from './panmove.js'
import Bus from "@/dragBase/event";

let PANMOVE, RULER, DEFAULTSCALE;

function ready(e) {
  if (e.code.toUpperCase() == 'SPACE') {
    PANMOVE.addMask()
    e.stopPropagation();
  }
}

function cancel() {
  PANMOVE.removeMask()
}

function move(e) {
  PANMOVE.move(e)
}

function mouseUp(e) {
  if (e.button === 0) {
    PANMOVE.lastPos(e)
  }
}

function mouseWheel(e) {
  if (e.ctrlKey === true || e.metaKey) {
    PANMOVE.zoom(e);
    e.preventDefault();
  }

}

export default {
  bind() {

  },
  inserted(el, binding) {
    DEFAULTSCALE = binding.value;
    let rulerHeight = 15
    RULER = el.ruler = new ruler({
      container: el,
      // reference to DOM element to apply rulers on
      rulerHeight: rulerHeight,
      // thickness of ruler
      fontFamily: 'arial',
      // font for points
      fontSize: '10px',
      strokeStyle: '#D6D6D6',
      lineWidth: 1,
      enableMouseTracking: true,
      enableToolTip: false
    })
    window.Ruler = RULER
    PANMOVE = el.panmove = new Panmove({
      zoomDom: el.querySelector('.layer-box'),
      eventDom: el,
      moveStop() {
        RULER.api.setPos({x: PANMOVE.$config.x - rulerHeight, y: PANMOVE.$config.y - rulerHeight})
      },
      move(e) {
        RULER.api.setPos({x: PANMOVE.$config.x - rulerHeight, y: PANMOVE.$config.y - rulerHeight})
      },
      cal($config) {
        let scale = parseFloat(parseFloat(1 / (DEFAULTSCALE * $config.scale)).toFixed(2));
        RULER.api.setScale(scale)
        RULER.api.setPos({x: $config.x - rulerHeight, y: $config.y - rulerHeight})
        Bus.$emit('rulerScale', $config.scale)
      }
    })

    RULER.api.setScale(parseFloat(1 / DEFAULTSCALE) || 1)
    RULER.api.setUnit(DEFAULTSCALE == 1 ? 'px' : '')
    //初始化居中
    Bus.$on('initTree', () => {
      Bus.$nextTick(() => {
        PANMOVE.center()
        PANMOVE.matrixTo();
        PANMOVE.lastPos();
        RULER.api.setPos({x: PANMOVE.$config.x - rulerHeight, y: PANMOVE.$config.y - rulerHeight})
      })
    })

    RULER.api.setPos({x: PANMOVE.$config.x - rulerHeight, y: PANMOVE.$config.y - rulerHeight})


    document.body.addEventListener('keydown', ready)
    document.body.addEventListener('keyup', cancel)
    document.addEventListener('mousemove', move)
    document.addEventListener('mouseup', mouseUp)
    document.addEventListener('mouseout', mouseUp)
    document.addEventListener('mousewheel', mouseWheel, {passive: false})
  },
  unbind() {
    document.body.removeEventListener('keydown', ready)
    document.body.removeEventListener('keyup', cancel)
    document.removeEventListener('mousemove', move)
    document.removeEventListener('mouseup', mouseUp)
    document.removeEventListener('mouseout', mouseUp)
    document.removeEventListener('mousewheel', mouseWheel)
  }
}