<template>
  <pdDragResizeRotate
      v-if="element"
      :pd-id="element.id"
      :x="mm2px(element.left)"
      :y="mm2px(element.top)"
      :w="mm2px(element.width)"
      :h="mm2px(element.height)"
      :lock="isLock"
      :z="zIndex"
      :r="element.rotate||0"
      :activeable="activeable"
      :active="element.active"
      :draggable="draggable(element.id)"
      :resizeable="resizeable(element)"
      :rotateable="rotateable(element)"
      :sticks="sticks"
      :rulerScale="rulerScale"
      @dragStop="dragStop"
      @dragStart="dragStart"
      @dragging="dragging"
      @resizeStart="resizeStart"
      @resizeStop="resizeStop"
      @rotateStart="rotateStart"
      @rotateStop="rotateStop"
      @activated="beforeSelect($event,element)"
      @dblclick.native="parentActiveChild"
      @refLineParams="(res)=>{$emit('refLineParams',res)}"
      :class="setDragDropBoxClass()"
  >
    <component :is="element.type" :renderData="element">
      <template v-if="element.children.length">
        <resizeable v-for="(el,$elIndex) in element.children"
                    :element.sync="el"
                    :key="el.id"
                    :zIndex="$elIndex"
                    :activeable="element.childActiveAble"
                    :shiftKey="shiftKey"
                    :scale="scale"
                    :rulerScale="rulerScale"
                    @parentSelect="beforeSelect"
                    @parentActiveChild="parentActiveChild"
                    @refLineParams="getRefLineParams"
        ></resizeable>
        <span class="ref-line v-line"
              v-for="item in vLine"
              :key="item.id"
              v-show="item.display"
              :style="{ left: item.position, top: item.origin, height: item.lineLength}"
        />
        <span class="ref-line h-line"
              v-for="item in hLine"
              :key="item.id"
              v-show="item.display"
              :style="{ top: item.position, left: item.origin, width: item.lineLength}"
        />
      </template>
    </component>
  </pdDragResizeRotate>
</template>

<script>
import dragdropbox from "@/dragBase/components/dragdropbox";

import pdDragResizeRotate from '@/dragBase/dragresize/dragresizeroate/dragresizeroate.vue'
import { isArray } from "@/dragBase/utils/utils";
import Bus from '@/dragBase/event'

export default {
  name: "resizeable",
  components: {pdDragResizeRotate, dragdropbox},
  props: {
    element: {
      type: Object,
      default: function () {
        return null
      }
    },
    zIndex: {
      type: Number,
      default: 0
    },
    activeable: {
      type: Boolean,
      default: true
    },
    shiftKey: {
      type: Boolean,
      default: false
    },
    scale: {
      type: [String, Number],
      default: 1
    },
    rulerScale: {
      type: [String, Number],
      default: 1
    }
  },
  computed: {
    selected() {
      return Bus.selected
    },
    isLock() {
      return this.shiftKey
    }
  },
  data() {
    return {
      orPos: {},
      diffPos: {left: 0, top: 0},
      vLine: [],
      hLine: [],
      sticks: ['tl', 'tm', 'tr', 'mr', 'ml', 'bl', 'bm', 'br', 'angle']
    }
  },
  methods: {
    getRefLineParams(params) {
      const {vLine, hLine} = params
      let id = 0
      this.vLine = vLine.map(item => {
        item[ 'id' ] = ++id
        return item
      })
      this.hLine = hLine.map(item => {
        item[ 'id' ] = ++id
        return item
      })
    },
    select(val) {
      Bus.$emit('select', val)
    },
    /*todo 这里貌似没有使用 需要核对*/
    // unSelect(val) {
    //   Bus.$emit('unSelect', val)
    // },
    // cleanSelect(val) {
    //   Bus.$emit('cleanSelect', val)
    // },
    dragStart(pos) {
      this.selected.forEach(el => {
        this.orPos[ el.id ] = {top: el.top, left: el.left}
      })
      this.diffPos = {left: pos.left, top: pos.top}
      Bus.$emit('recordShot')
    },
    dragging(pos) {
      let {top, left} = pos
      //orDiff
      if (this.selected.length > 1) {
        let diffTop = top - this.diffPos.top;
        let diffLeft = left - this.diffPos.left;
        this.selected.forEach(el => {
          if (el.id !== this.element.id) {
            let left = this.orPos[ el.id ].left + this.px2mm(diffLeft)
            let top = this.orPos[ el.id ].top + this.px2mm(diffTop)
            Bus.$emit('updateItem', {id: el.id, data: {left, top}, updateGroupBox: false})
          }
        })
      }
    },
    dragStop(pos) {
      if (this.selected.length === 1) {
        let {left, top, width, height, rotate} = pos
        left = this.px2mm(left)
        top = this.px2mm(top)
        width = this.px2mm(width)
        height = this.px2mm(height)
        Bus.$emit('updateItem', {id: this.element.id, data: {left, top, width, height, rotate}, updateGroupBox: true})
      } else {
        let {left, top, width, height, rotate} = pos
        left = this.px2mm(left)
        top = this.px2mm(top)
        width = this.px2mm(width)
        height = this.px2mm(height)
        Bus.$emit('updateItem', {id: this.element.id, data: {left, top, width, height, rotate}, updateGroupBox: false})
        Bus.$emit('updateGroup')
      }
    },
    resizeStart() {
      Bus.$emit('recordShot')
    },
    resizeStop(pos) {
      let {left, top, width, height, rotate} = pos
      left = this.px2mm(left)
      top = this.px2mm(top)
      width = this.px2mm(width)
      height = this.px2mm(height)
      Bus.$emit('updateSelected', {left, top, width, height, rotate})
      Bus.$emit('updateGroup')
    },
    rotateStart() {
      Bus.$emit('recordShot')
    },
    rotateStop(pos) {
      let {left, top, width, height, rotate} = pos
      left = this.px2mm(left)
      top = this.px2mm(top)
      width = this.px2mm(width)
      height = this.px2mm(height)
      Bus.$emit('updateSelected', {left, top, width, height, rotate})
      Bus.$emit('updateGroup')
    },
    beforeSelect($event = {}) {
      let el = this.element
      if (this.activeable) {
        this.select({event: $event, id: el.id})
      } else {
        this.$emit('parentSelect', $event, el)
      }
    },
    parentActiveChild() {
      if (this.activeable) {
        if (this.element.children.length) {
          if (this.element.active) {
            if (isArray(this.selected) && this.selected.length > 1) {
              this.cleanSelect()
              this.select({id: this.element.id})
            } else {
              this.element.childActiveAble = true;
            }
          }
        }
      } else {
        this.$emit('parentActiveChild')
      }

    },
    draggable(id) {
      return this.selected.findIndex(el => {
        return el.id === id
      }) > -1
    },
    resizeable(ele) {
      let id = ele.id
      if (ele.type == 'dragdropbox') {
        return false
      } else {
        if (this.selected.length > 1) {
          return false
        } else {
          return this.selected.findIndex(el => {
            return el.id === id
          }) > -1
        }
      }
    },
    rotateable(ele) {
      let id = ele.id

      if (ele.type == 'dragdropbox') {
        return false
      } else {
        if (this.selected.length > 1) {
          return false
        } else {
          let result = ( this.element.rotateable && this.selected.findIndex(el => {
            return el.id === id
          }) > -1 ) || false;
          return result
        }
      }
    },
    setDragDropBoxClass() {
      if (this.element.type == 'dragdropbox' && this.element.active && !this.element.childActiveAble) {
        return 'box-active dragdropbox'
      } else if (this.element.type == 'dragdropbox' && this.element.active && this.element.childActiveAble) {
        return 'dragdropbox'
      } else if (this.element.type == 'dragdropbox' && !this.element.active) {
        return 'noborder dragdropbox'
      } else {
        return ''
      }
    },
    mm2px(mm = 0) {
      return parseFloat(( Number(mm) * this.scale ).toFixed(1))
    },
    px2mm(px = 0) {
      return parseFloat(( Number(px) / this.scale ).toFixed(1))
    }
  }
}
</script>

<style scoped lang="scss">
.child-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.select {
  border: 1px dashed #0a90ea;
}

.box-active {
  border: 1px dashed #dae79a;
}
</style>