<template>
  <div class="sketch-box"
       @click="(e)=>{cleanSelect({e})}"
       v-pd-ruler="ruleConfig.scale"
       v-frameselect="{excluded:$refs.box,selected:frameselectEvt,type:ruleConfig.frameSelectType}">
    <div class="layer-box">
      <div class="layer-content" :style="getLayerSize">
        <template>
          <div class="sketch-stage">
            <slot></slot>
          </div>
        </template>
        <div class="item-box" ref="box">
          <resizeable
              v-for="(element,$index) in sketchupData.children"
              :z-index="$index"
              :key="element.id"
              :element.sync="element"
              :shiftKey="shiftKey"
              :scale="ruleConfig.scale"
              :rulerScale="rulerScale"
              ref="resizeable"
              @refLineParams="getRefLineParams"
          >
          </resizeable>
          <span class="ref-line v-line"
                v-for="item in vLine"
                :key="item.id"
                v-show="item.display"
                :style="{ left: item.position, top: item.origin, height: item.lineLength}"
          />
          <span class="ref-line h-line"
                v-for="item in hLine"
                :key="item.id"
                v-show="item.display"
                :style="{ top: item.position, left: item.origin, width: item.lineLength}"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import resizeable from "@/dragBase/dragresize/resizeable/resizeable";
import Bus from '@/dragBase/event'
import pdRuler from "@/dragBase/dragresize/directives/pdruler/pdruler";
import frameselect from "@/dragBase/dragresize/directives/frameselect/frameselect";
import mixins from "@/dragBase/pdDragBase/mixins";

export default {
  name: "pdDragBase",
  mixins: [mixins],
  directives: {pdRuler: pdRuler, frameselect: frameselect},
  components: {resizeable},
  beforeMount() {
    //默认初始化
    this.initTree({
      data: {
        type: 'sketchup',
        width: 10,
        height: 10,
        to: 0,
        left: 0,
        children: []
      },
      snapShot: false
    })
    Bus.$on('rulerScale', res => {
      this.rulerScale = res;
    })
  },
  data() {
    return {
      vLine: [],
      hLine: [],
      rulerScale: 1
    }
  },
  computed: {
    selected() {
      return Bus.selected
    },
    sketchupData() {
      return Bus.state.sketchup
    },
    getLayerSize() {
      let sketch = Bus.state.sketchup
      return {
        width: sketch.width * this.ruleConfig.scale + 'px',
        height: sketch.height * this.ruleConfig.scale + 'px'
      }
    }
  },
  props: {
    beforeCopy: {
      type: Function,
      default: function (val) {
        return new Promise(( (resolve) => {
          //对 复制数据异步加工 val
          resolve(val)
        } ))
      }
    },
    beforeDelete: {
      type: Function,
      default() {
        return function () {
          return true
        };
      }
    },
    beforeAdd: {
      type: Function,
      default() {
        return null
      }
    },
    ruleConfig: {
      type: Object,
      default() {
        return {
          scale: 1,
          frameSelectType: 'fit',
          stackLength: 30
        }
      }
    }
  },
  methods: {
    /**
     * 对齐线
     *
     * */
    getRefLineParams(params) {
      const {vLine, hLine} = params
      let id = 0
      this.vLine = vLine.map(item => {
        item[ 'id' ] = ++id
        return item
      })
      this.hLine = hLine.map(item => {
        item[ 'id' ] = ++id
        return item
      })
    },
    /**
     * 框选
     * */
    frameselectEvt(selectedMap) {
      if (Object.keys(selectedMap).length) {
        this.$fire('cleanSelect');
        for ( let key in selectedMap ) {
          // let el = this.sketchupData.children[this.sketchupData.children.findIndex(ele => {
          //   return ele.id == key
          // })]
          //需要伪造 ctrl 点选
          this.$fire('select', {event: {ctrlKey: true}, id: key})
        }
      }
    },

    /**
     * 事件派发
     * */
    $fire(eventName, playLoad, snapShot = false) {
      snapShot && Bus.$emit('recordShot')
      Bus.$emit(eventName, playLoad)
    },
    /**
     * 事件绑定
     * */
    $bind(eventName, callback) {
      Bus.$on(eventName, callback)
    },
    /**
     * 事件取消绑定
     * */
    $unbind(eventName, callback) {
      Bus.$off(eventName, callback)
    }
  },
  updated() {
    if (Bus.state.snapshotLength !== this.ruleConfig.stackLength) {
      Bus.updateConfig({snapshotLength: this.ruleConfig.stackLength})
    }
  }
}


</script>

<style scoped lang="scss">
.sketch-box {
  background: #EBEBEB;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
}

.layer-box {
  position: absolute;
  width: fit-content;
  height: fit-content;
  transform-origin: left top;

  .layer-content {
    position: relative;
    overflow: visible;
    z-index: 2;
  }

  .sketch-stage {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('data:image/svg+xml;base64,');
    background-size: 16px;
    background-position: 0, 0;
    transform-origin: 0 0;
  }

  .item-box {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

}


</style>