<template>
  <div class="drag-drop-box">
    <slot></slot>
  </div>
</template>

<script>
import { getAxis } from "@/dragBase/utils/utils";
import Bus from '@/dragBase/event'

export default {
  name: "dragdropbox",
  props: {
    renderData: {
      type: Object,
      required: true
    }
  },
  mounted () {
    Bus.$on('updateGroup', this.trigger)
  },
  methods:{
    trigger(){
      this.$nextTick(()=>{
        Bus.$emit('updateGroupBox', {id: this.renderData.id, coord: {...getAxis(this.renderData.children)}})
      })
    }
  },
  beforeDestroy() {
    Bus.$off('updateGroup',this.trigger)
  }

}
</script>

<style scoped lang="scss">
.drag-drop-box {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

</style>