/**
 * Created by k186 on 2020/12/28.
 * Name: 框选 只会框选excluded 的一级子集，触发边界由border确认
 * GitHub:
 * Email: k1868548@gmail.com
 */
import './frameselect.scss'
import {intersect, throttle, hasClass, cleanSelect} from '@/dragBase/utils/utils'

let border = 'body'
let type = 'fit';
let excluded = null;
let active = false;
let selected = null;
let result = {};
let startPos = {
  left: 0,
  top: 0,
  width: 0,
  height: 0
}
let box = document.createElement('div')
box.setAttribute('class', 'pd-frame-box')
export default {
  bind() {

  },
  inserted(el) {
    el.addEventListener('mousedown', mousedown)
    document.addEventListener('mousemove', mousemove)
    document.addEventListener('mouseup', mouseup)
  },
  update(el, binding) {
    selected = typeof binding.value.selected == 'function' ? binding.value.selected : null
    border = el
    excluded = binding.value.excluded || null
    type = binding.value.type || type
  },
  componentUpdated() {

  },
  unbind(el) {
    el.removeEventListener('mousedown', mousedown)
    document.removeEventListener('mousemove', mousemove)
  }
}

function mousedown(e) {
  if (e.button !== 0) {
    return
  }
  if (hasClass(e.target, 'vdr')) {
    return
  }
  if (border === 'body') {
    border = document.querySelector(border)
  }
  let rect = border.getBoundingClientRect()
  if (!intersect(rect, rect, 'pointer', e)) {
    active = false
    return
  }
  result = {}
  active = true
  startPos = {left: e.pageX, top: e.pageY, width: 0, height: 0}
  box.style.left = startPos.left + 'px';
  box.style.top = startPos.top + 'px';
  box.style.width = startPos.width;
  box.style.height = startPos.height;
}


function mousemove(e) {
  if (!active) {
    return
  }
  if (active && (Math.abs(e.pageX - startPos.left) >= 10)) {
    if (!document.body.contains(box)) {
      document.body.append(box);
    }
  }
  cleanSelect()
  if (e.pageX >= startPos.left) {
    box.style.width = (e.pageX - startPos.left + 1) + 'px';
  }
  if (e.pageX < startPos.left) {
    box.style.left = e.pageX + 'px';
    box.style.width = (startPos.left - e.pageX) + 'px';
  }
  if (e.pageY >= startPos.top) {
    box.style.height = (e.pageY - startPos.top + 1) + 'px';
  }
  if (e.pageY < startPos.top) {
    box.style.top = e.pageY + 'px';
    box.style.height = (startPos.top - e.pageY) + 'px';
  }
  if (excluded && excluded.children.length) {
    throttle(getTouchBox, 50)();
  }
}

function mouseup(e) {
  if (!active) {
    return
  }
  active = false;
  box.remove();
  //cleanSelect()
  if (selected && Object.keys(result).length) {
    selected(result)
  }
}

function getTouchBox() {
  result = {};
  excluded.childNodes.forEach(el => {
    if (el.hasAttribute('pd-id') && intersect(el.getBoundingClientRect(), box.getBoundingClientRect(), type)) {
      result[el.getAttribute('pd-id')] = true
    }
  })
}


