
import dtext from "@/customComponents/dtext/dtext.vue"
import root from "@/customComponents/root/root.vue"


const components = [
  dtext,
  root
]

const install = function (Vue) {
  if (install.installed) return
  components.map(component => {
    Vue.component(component.name, component)
  });
  if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
  }
};
export default {
  install,
  components
}